import logo from './logo.svg';
import './App.css';
import Button from '@mui/material/Button';
import {useState, useEffect} from 'react';
import { styled } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import Fab from '@mui/material/Fab';
import { deepPurple, teal } from '@mui/material/colors';
import MenuIcon from '@mui/icons-material/Menu';
import AddIcon from '@mui/icons-material/Add';
import SearchIcon from '@mui/icons-material/Search';
import MoreIcon from '@mui/icons-material/MoreVert';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PropTypes from 'prop-types';
import CssBaseline from '@mui/material/CssBaseline';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import Container from '@mui/material/Container';
import Slide from '@mui/material/Slide';
function HideOnScroll(props) {
  const { children, window } = props;
  
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
    window: PropTypes.func,
  });

  return (
    !trigger?<Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>:<></>
  );
}

HideOnScroll.propTypes = {
  children: PropTypes.element.isRequired,
};

const outerTheme = createTheme({
  palette: {
    primary: {
      main: deepPurple[500],
    },
    secondary: {
      main: teal[400],
    },
  },
});
const StyledToolbar = styled(Toolbar)(({ theme }) => ({
  alignItems: 'flex-start',
  paddingTop: theme.spacing(8),
  paddingLeft: theme.spacing(4),
  paddingBottom: theme.spacing(8),
  // Override media queries injected by theme.mixins.toolbar
}));

function App() {
  const [value, setValue] = useState('1');
  const [lock, setLock] = useState(false);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const [scrollTop, setScrollTop] = useState(0)
  const [trigger, setTrigger] = useState(false)
  const init = () => {
    const onScroll = e => {
      if(lock) return;
      setScrollTop(e.target.documentElement.scrollTop);
      if(e.target.documentElement.scrollTop > 0) {
        setTrigger(true)
        setLock(true);
        setTimeout(() => setLock(false), 1000)
      };
    };
    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }
  useEffect(init, [scrollTop]);
  return (
    <div className="App">
      <ThemeProvider theme={outerTheme}>
      

      <Slide in={!trigger}>
        <AppBar position="static" color="primary">
        <StyledToolbar style={{justifyContent: 'center'}}>
          
        <Stack sx={{ alignItems: "center", minHeight: trigger?0:'50vh', flexDirection: 'column'}}  style={{ justifyContent: 'center'}}>
        <Typography variant="h3" component="div" sx={{ fontWeight: 500, }}>
            Socrates
          </Typography>
          <Typography variant="h6" component="div" sx={{ alignText: 'center', marginTop: 4}}>
            
          </Typography>
          
          <CssBaseline />
          </Stack>
        </StyledToolbar>
      </AppBar>
      </Slide>
      <Slide in={trigger}><AppBar position="fixed" color="primary"  >
        <Toolbar sx={{  justifyContent: "space-between" }}>
          
        <Typography variant="h6" component="div" noWrap >
            Socrates
          </Typography>
          <CssBaseline />
          <IconButton
            size="large"
            aria-label="display more actions"
            edge="end"
            color="inherit"
            onClick={() => {init()}}
          >
            <MoreIcon />
          </IconButton>
        </Toolbar>
      </AppBar ></Slide>
      
      
      
      <Box sx={{ width: '100%', typography: 'body1', marginTop: !trigger?'0' : '-192px'}}>
      <TabContext value={value}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <TabList onChange={handleChange} aria-label="lab API tabs example">
            <Tab label="" value="1" />
            <Tab label="" value="2" />
          </TabList>
        </Box>
        <TabPanel value="1"></TabPanel>
        <TabPanel value="2"></TabPanel>
      </TabContext>
      </Box>
      
      <Container>
        
      </Container>
      <Fab color="secondary" style={{
  position: 'absolute',
  bottom: 16,
  right: 16,
}}>
        <PlayArrowIcon />
      </Fab>
      </ThemeProvider>
    </div>
  );
}

export default App;
